export default function Testimonials() {
  const items = [
    {
      quote: "Uswift saved me hours every week — auto-applying and keeping my profile materials in sync made job hunting painless.",
      name: 'Aisha K.',
      title: 'Product Manager',
      avatar: '/avatars/avatar1.svg',
    },
    {
      quote: "The browser extension is incredibly fast and reliable. The dashboard gives me clear insights into my applications.",
      name: 'Liam R.',
      title: 'Software Engineer',
      avatar: '/avatars/avatar2.svg',
    },
    {
      quote: "I love the Profile Vault — switching cover letters and resumes for different roles is effortless.",
      name: 'Sofia M.',
      title: 'UX Designer',
      avatar: '/avatars/avatar3.svg',
    },
    {
      quote: "Auto-apply saved me from repetitive form fills. I can focus on tailoring my resume instead.",
      name: 'Noah P.',
      title: 'Data Analyst',
      avatar: '/avatars/avatar4.svg',
    },
    {
      quote: "Profile Vault made managing multiple resumes easy and painless.",
      name: 'Maya L.',
      title: 'Marketing Specialist',
      avatar: '/avatars/avatar5.svg',
    },
    {
      quote: "The job tracking dashboard gives me exactly the insights I need.",
      name: 'Ethan V.',
      title: 'Sales Engineer',
      avatar: '/avatars/avatar6.svg',
    },
    {
      quote: "I appreciate the simple, focused UX — it does one thing well.",
      name: 'Olivia T.',
      title: 'QA Engineer',
      avatar: '/avatars/avatar7.svg',
    },
    {
      quote: "Helps me stay organized and consistent across applications.",
      name: 'Carlos D.',
      title: 'Frontend Developer',
      avatar: '/avatars/avatar8.svg',
    },
  ];

  return (
    <section className="py-16 px-4 bg-gray-50">
      <div className="max-w-6xl mx-auto">
        <h2 className="text-3xl font-bold mb-6 text-center text-uswift-navy">What early users say</h2>

        <div className="grid gap-6 grid-cols-1 md:grid-cols-3">
          {items.map((t, i) => (
            <blockquote key={i} className="bg-white p-6 rounded-lg shadow flex flex-col card-magic">
              <div className="flex items-center gap-4 mb-4">
                <img src={t.avatar} alt={`${t.name} avatar`} className="w-12 h-12 rounded-full object-cover" />
                <div>
                  <div className="font-semibold text-gray-900">{t.name}</div>
                  <div className="text-sm text-gray-600">{t.title}</div>
                </div>
              </div>
              <p className="text-gray-800">“{t.quote}”</p>
            </blockquote>
          ))}
        </div>
      </div>
    </section>
  );
}
